import React, { useEffect ,useState} from "react";
import ReactDOM from "react-dom";
import { CSSTransition } from "react-transition-group";
import "./Modal.css";
import axios from "axios";

import { useDispatch, useSelector } from "react-redux"

const ModalBroker = (props) => {

  const [modaldata, setmodaldata] = useState(props.filteredData);
  const [update, setUpdate] = useState(false);
  const API_URL = process.env.REACT_APP_SERVER_URL;
  const handleSoldstatus =async(el,status)=>{
    setUpdate(true)
    const id = el.id;
    const value=status;
    
    await axios
    .put(`${API_URL}/updatesold`, {
      id: id,
      status:value
    })
    .then((response) => {

      axios
      .get(`${API_URL}/catalougedata`)
      .then((response) => {
        
        props.onClose();
    setmodaldata(response.data)
    // console.log(modaldata);
     
      })
      .catch((error) => {
       
        console.log(error);
      });
    
    })
    .catch((error) => {
      console.log(error);
    });
    
    }
  // console.log(props);
  const closeOnEscapeKeyDown = e => {
    if ((e.charCode || e.keyCode) === 27) {
      props.onClose();
    }
  };

  useEffect(() => {
    document.body.addEventListener("keydown", closeOnEscapeKeyDown);
    return function cleanup() {
      document.body.removeEventListener("keydown", closeOnEscapeKeyDown);
    };
  }, []);

return(
    <>
    <CSSTransition
      in={props.show}
      unmountOnExit
      timeout={{ enter: 0, exit: 300 }}
    >
      <div className="modal" onClick={props.onClose}>
        <div className="modal-content" onClick={e => e.stopPropagation()}>
          <div className="modal-header">
            <h4 className="modal-title">{props.title}</h4>

           
          </div>
          <div className="modal-body">


             <table class="modaltable table-bordered">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Lot</th>
              <th scope="col">Factory</th>
              <th scope="col">Price</th>
              <th scope="col">Bidder</th>
              <th scope="col">Status</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody>
            {props.filteredData.map((el, index) => {
              return (
                <tr>
                  <td scope="row"><h4>{index + 1}</h4></td>
                  <td><h4>{el.lot}</h4></td>
                  <td><h4>{el.factory_name}</h4></td>
                  <td><h4>{el.maximum_bid}</h4></td>
                  <td><h4>{el.buyer_name}</h4></td>
                  <td>
                    <span className={el.sold_status === 1 ? "hname" : el.sold_status === 2 ? "rname" : "rname"}>
                      {el.sold_status === 1 ? "Sold" :  el.sold_status === 2 ?"Unsold":"Pending"}
                    </span>
                  </td>
                  <td>
                  <button type="button"  onClick={()=>props.onV(el,1)} class="btn btn-success px-3"><i class="fas fa-check" aria-hidden="true"></i></button>
                  <button type="button"  onClick={()=>props.onV(el,2)} class="btn btn-danger px-3"><i class="fas fa-times" aria-hidden="true"></i></button>
                  <button type="button"  onClick={()=>props.onV(el,0)} class="btn btn-info px-3"><i class="fas fa-spinner" aria-hidden="true"></i></button>
             
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
          </div>
          <div className="modal-footer">
            <button onClick={props.onClose} className="btn btn-danger">
              Close
            </button>
          </div>
        </div>
      </div>
    </CSSTransition>,
  </>
  );
};

export default ModalBroker;
