import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios, * as others from "axios";
import swal from "sweetalert";
const initialState = {
  loading: false,
  lc_data: [],
  status: "idle",
  error: false,
  edit:false,
  refresh:false,
};

const API_URL = process.env.REACT_APP_SERVER_URL;

export const saveData = createAsyncThunk("lcopen/create", async (lcopen) => {
  const response = await axios.post(`${API_URL}/createlcac`, {
    adata: lcopen
  });
  return response.data;
});

export const updateData = createAsyncThunk("lcopen/updatedata", async (data) => {
console.log(data);
  const response = await axios.put(`${API_URL}/updatelcac`, {
    data: data,
  });
  return response.data;
});

export const selectData = createAsyncThunk("lcopen/selectdata", async (props) => {
  const response= await axios.put(`${API_URL}/selectlcac`,{
    id: props,
  });
    return response.data;
  });
  export const deleteData = createAsyncThunk("lcopen/deletedata", async (props) => {
    const response =axios.put(`${API_URL}/deletelcac`,{
      id: props,
    })
    return response.data;
      
    });
 
    
const lcopeningSlice = createSlice({
  name: "lcaccount",
  initialState,
  extraReducers: {
    [saveData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [saveData.fulfilled]: (state, action) => {
      const { payload } = action;
    },
    [saveData.rejected]: (state) => {
      state.error = true;
    },
    [updateData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [updateData.fulfilled]: (state) => {
      state.edit=false;
    },
    [updateData.rejected]: (state) => {
      state.error = true;
    },
    [selectData.fulfilled]: (state,action) => {
      const { payload } = action;
      state.lc_data = payload;
      state.edit=true;
    },
    [deleteData.pending]: (state) => {
      state.refresh=false;
    },
    [deleteData.fulfilled]: (state) => {
      state.refresh=true;
    },
  },
});

export const {} = lcopeningSlice.actions;
const lcopeningReducer = lcopeningSlice.reducer;

export default lcopeningReducer;
