import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios, * as others from "axios";
import swal from "sweetalert";
const initialState = {
  loading: false,
  supply_cart: [],
  status: "idle",
  error: false,
  edit:false,
  refresh:false,
};

const API_URL = process.env.REACT_APP_SERVER_URL;

export const saveData = createAsyncThunk("savedata", async (supplystore) => {
  const response = await axios.post(`${API_URL}/allsell`, {
    adata: supplystore,
  });
  return response.data;
});

export const updateData = createAsyncThunk("updatedata", async (supplystore) => {
  const response = await axios.post(`${API_URL}/allsellupdate`, {
    adata: supplystore,
  });
  return response.data;
});

export const selectData = createAsyncThunk("selectdata", async (props) => {
  const response= await axios.put(`${API_URL}/selectselldata`,{
    id: props,
  });
    return response.data;
  });
  export const deleteData = createAsyncThunk("deletedata", async (props) => {
 
    const response =axios.put(`${API_URL}/deletesellb2`,{
      id: props,
    })
    return response.data;
      
    });
 
    
const supplySlice = createSlice({
  name: "supply",
  initialState,
  extraReducers: {
    [saveData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [saveData.fulfilled]: (state, action) => {
      const { payload } = action;
    },
    [saveData.rejected]: (state) => {
      state.error = true;
    },
    [updateData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [updateData.fulfilled]: (state, action) => {
      const { payload } = action;
      state.edit=false;
    },
    [updateData.rejected]: (state) => {
      state.error = true;
    },
    [selectData.fulfilled]: (state,action) => {
      const { payload } = action;
      state.supply_cart = payload;
      state.edit=true;
    },
    [deleteData.pending]: (state) => {
      state.refresh=false;
    },
    [deleteData.fulfilled]: (state) => {
      state.refresh=true;
    },
  },
});

export const {} = supplySlice.actions;
const supplyReducer = supplySlice.reducer;

export default supplyReducer;
