
import React, { useState, useEffect, useRef, useContext } from "react";
import { Context } from "../../context/Store";
import { useDispatch, useSelector } from "react-redux";
import { getuser } from "../../store/loginSlice";
import { getAllcatalogue, getAllfactory,saveData, updateData, selectData } from "../../store/catalogueSlice";
import swal from "sweetalert";
const AddForm = () => {
  const API_URL = process.env.REACT_APP_SERVER_URL;
  const { factory_list, edit,catalogue_data } = useSelector((state) => state.catalogue);
  const { user_data } = useSelector((state) => state.login);
  const usersString = localStorage.getItem('users');
  // const users = JSON.parse(usersString);
  const [users, setUsers] = useState([]);
  const dispatch = useDispatch();
  const usersd = JSON.parse(usersString);
  const [catalogueData, setcatalogueData] = useState({
    lot: "",
    invoice: "",
    factory: "",
    grade: "",
    net_weight: "",
    price: "",
    pkgs: "",
    totalkg: "",
    brokers: usersd.link_id,
    warehouse: "",
    category: "",
    sale_no:"",
    posted: usersd.username,
  });
  
  

  let name, value;
  const getUserData = (event) => {
    name = event.target.name;
    value = event.target.value;

    setcatalogueData({ ...catalogueData, [name]: value });
  };
  const warehouse = factory_list.filter(
    (ware) => ware.type == "WAREHOUSE" 
  );
  const factory = factory_list.filter(
    (factory) => factory.type == "FACTORY" 
  );
  const addAccounts =async (e) => {
    const usersString = localStorage.getItem('users');
    const usersd = JSON.parse(usersString);
    // console.log(usersd);
    // console.log(user_data);
    if (!edit) {
      e.preventDefault();
      // dataInsert(addurl,cataloguedata);
      
      await dispatch(saveData(catalogueData));
      
      setcatalogueData({
        lot: "",
        invoice: "",
        factory: "",
        grade: "",
        net_weight: "",
        price: "",
        pkgs: "",
        totalkg: "",
        brokers: usersd.link_id,
        warehouse: "",
        category: "",
        sale_no:"",
        posted: usersd.username,
      });
      await dispatch(getAllcatalogue());
      swal({
        title: "Success",
        text: "Catalogue created successfully!!",
        icon: "success",
        timer: 2500,
        button: false,
      });
      
      // // setAccountList([...cataloguedata,accountList])
    }
  };

  const updateAccount = (e) => {
    const usersString = localStorage.getItem('users');
    const usersd = JSON.parse(usersString);
    if (edit) {
      e.preventDefault();
      dispatch(updateData(catalogueData));
      swal({
        title: "Success",
        text: "Accounts updated successfully!!",
        icon: "success",
        timer: 2500,
        button: false,
      });
      setcatalogueData({
        lot: "",
        invoice: "",
        factory: "",
        grade: "",
        net_weight: "",
        price: "",
        pkgs: "",
        totalkg: "",
        brokers: usersd.link_id,
        warehouse: "",
        category: "",
        sale_no:"",
        posted: usersd.username,
      });
      dispatch(getAllcatalogue());
    }
  };

  useEffect(() => {
    const usersString = localStorage.getItem('users');
    const users = JSON.parse(usersString);
    
    if(users.link_id===null){
      window.location.href = '/';
    }
    setUsers(users)
    // console.log(users);
 dispatch(getAllfactory());
 dispatch(getuser());
 dispatch(getAllcatalogue());
  }, [dispatch]);
  useEffect(() => {
    // console.log(edit);
    if (edit) {
      setcatalogueData(catalogue_data[0]);
      // console.log(catalogue_data);
    }
  }, [edit]);
  
  return (
    <>
  
      <section className="col-lg-7 connectedSortable">
        <div className="box box-danger">
          {/* Tabs within a box */}
          <div className="tab-content no-padding">
            {/* Morris chart - Sales */}
            <div className="box-header header-custom">
              <h3 className="box-title">Account Info</h3>
            </div>
            <div className="box-body form-horizontal">
              <div className="row">
                <div class="col-md-6">
                 
                  <div className="form-group">
                    <label for="accntno" className="col-sm-3 control-label">
                      Lot
                    </label>
                    <div className="col-sm-9">
                   
                          <input
                            required
                            className="form-control"
                            type="text"
                            value={catalogueData.lot}
                            name="lot"
                            placeholder="ID"
                            autocomplete="off"
                            onChange={getUserData}
                            id=""
                          />
                    </div>
                  </div>
                  <div className="form-group">
                    <label for="proprietor" className="col-sm-3 control-label">
                      Invoice
                    </label>
                    <div className="col-sm-9">
                      <input
                        required
                        className="form-control"
                        type="text"
                        value={catalogueData.invoice}
                        name="invoice"
                        placeholder="Invoice"
                        autocomplete="off"
                        onChange={getUserData}
                        id=""
                      />
                    </div>
                  </div>     
                  <div className="form-group">
                    <label for="type" className="col-sm-3 control-label">
                      Factory
                    </label>
                    <div className="col-sm-9">
                      <select
                        required
                        value={catalogueData.factory}
                        onChange={getUserData}
                        name="factory"
                        autocomplete="off"
                        id="factory"
                        className="form-control"
                      >
                        <option value=""> -- Select factory -- </option>
            
                        {factory.map((shead) => (
                          <option value={shead.id}>{shead.name}</option>
                        ))}
                      </select>
                    </div>
                  </div>

                  <div className="form-group">
                    <label for="subaddress" className="col-sm-3 control-label">
                      Grade
                    </label>
                    <div className="col-sm-9">
                    <select
                        required
                        value={catalogueData.grade}
                        onChange={getUserData}
                        name="grade"
                        autocomplete="off"
                        id="grade"
                        className="form-control"
                      >
                        <option value=""> -- Select Grade -- </option>
                        {/* Mapping through each fruit object in our fruits array
                 and returning an option element with the appropriate attributes / values.
                          */}
                       <option value="FP">FP</option>
                       <option value="FBOP">FBOP</option>
                       <option value="BOP">BOP</option>
                       <option value="GBOP">GBOP</option>
                       <option value="OF">OF</option>
                       <option value="FOF">FOF</option>
                       <option value="PF">PF</option>
                       <option value="RD">RD</option>
                       <option value="PD">PD</option>
                       <option value="CD">CD</option>
                      </select>
                    </div>
                  </div>
                  <div className="form-group">
                    <label for="address" className="col-sm-3 control-label">
                      Net Weight
                    </label>
                    <div className="col-sm-9">
                      <input
                        required
                        className="form-control"
                        type="text"
                        value={catalogueData.net_weight}
                        name="net_weight"
                        placeholder="Weight"
                        autocomplete="off"
                        onChange={getUserData}
                        id=""
                      />
                    </div>
                  </div>
                  <div className="form-group">
                    <label for="address" className="col-sm-3 control-label">
                      Unit Value
                    </label>
                    <div className="col-sm-9">
                      <input
                        required
                        className="form-control"
                        type="text"
                        value={catalogueData.price}
                        name="price"
                        placeholder="price"
                        autocomplete="off"
                        onChange={getUserData}
                        id=""
                      />
                    </div>
                  </div>
                  <div className="form-group">
                    <label for="contact" className="col-sm-3 control-label">
                      Pkgs
                    </label>
                    <div className="col-sm-9">
                      <input
                        required
                        className="form-control"
                        type="text"
                        value={catalogueData.pkgs}
                        name="pkgs"
                        placeholder="packages"
                        autocomplete="off"
                        onChange={getUserData}
                        id=""
                      />
                    </div>
                  </div>
             





                </div>

                      <div className="col-md-6">
                    <div className="form-group">
                        <label for="accntno" className="col-sm-3 control-label">Total (Kgs)</label>
                        <div className="col-sm-9">
                          <input
                            required
                            className="form-control"
                            type="text"
                            value={catalogueData.totalkg}
                            name="totalkg"
                            placeholder="Total"
                            autocomplete="off"
                            onChange={getUserData}
                            id=""
                          />
                        </div>
                      </div>
                      <div className="form-group">
                    <label for="type" className="col-sm-3 control-label">
                      Warehouse
                    </label>
                    <div className="col-sm-9">
                      <select
                        required
                        value={catalogueData.warehouse}
                        onChange={getUserData}
                        name="warehouse"
                        autocomplete="off"
                        id="warehouse"
                        className="form-control"
                      >
                        <option value=""> -- Select Type -- </option>
                        {/* Mapping through each fruit object in our fruits array
                 and returning an option element with the appropriate attributes / values.
                          */}
                        {warehouse.map((shead) => (
                          <option value={shead.id}>{shead.name}</option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="form-group">
                    <label for="type" className="col-sm-3 control-label">
                      Category
                    </label>
                    <div className="col-sm-9">
                      <select
                        required
                        value={catalogueData.category}
                        onChange={getUserData}
                        name="category"
                        autocomplete="off"
                        id="category"
                        className="form-control"
                      >
                        <option value=""> -- Select Type -- </option>
                        {/* Mapping through each fruit object in our fruits array
                 and returning an option element with the appropriate attributes / values.
                          */}
                       <option value="DUST">DUST</option>
                       <option value="LEAF">LEAF</option>
                       <option value="SUPPLIMENT">SUPPLIMENT</option>
                      </select>
                    </div>
                  </div>
                      <div className="form-group">
                        <label for="accntno" className="col-sm-3 control-label">Sale no</label>
                        <div className="col-sm-9">
                          <input
                            required
                            className="form-control"
                            type="text"
                            value={catalogueData.sale_no}
                            name="sale_no"
                            placeholder="Sale no"
                            autocomplete="off"
                            onChange={getUserData}
                            id=""
                          />
                        </div>
                      </div>
                    
</div>


              </div>
            </div>

            {/* /.box-body */}
            <div className="box-footer">
              <button type="submit" className="btn btn-danger">
                Cancel
              </button>
              <input
                type="button"
                name="save"
                className="btn btn-success pull-right"
                defaultValue={edit ? "Update" : "Save"}
                id="butsave"
                onClick={edit ? updateAccount : addAccounts}
              />
            </div>
            {/* /.box-footer */}
          </div>
        </div>
      </section>
      <section className="col-lg-5 connectedSortable">
      <table className="">
  <tr>
    <th>Total Lot</th>
    <th>Total Kg</th>
    <th>Sold</th>
    <th>Unsold</th>
  </tr>
  <tr>
    <td>10</td>
    <td>100</td>
    <td>5</td>
    <td>5</td>
  </tr>
</table>
      </section>
    </>
  );
};

export default AddForm;
