import React,{useEffect,useContext} from 'react';
import { Switch , Link, Route, useHistory, BrowserRouter } from "react-router-dom";
import Axios from "axios";
const Dashboard = () => {
  const history = useHistory();
  const API_URL = process.env.REACT_APP_SERVER_URL;
  useEffect(()=>{
		Axios.get(`${API_URL}/verify`)
		.then(res => {
			if(res.data.Status === "Success") {
        history.push("/dashboard")
			} else {
        history.push("/")
			}
		})
	}, [])

    return (
        <>
       
          <section className="content-header">
  <h1 className="marquee">
    <marquee behavior="scroll" direction="left" scrollamount={5}>
      <p align="center" style={{fontSize: 20, color: 'yellow'}}> Tea e-auction is an online platform for buying and selling tea products. It allows tea producers and buyers to participate in an online auction process for buying and selling tea products. The e-auction system provides a more efficient and transparent method for trading tea products compared to the traditional auction system. The platform offers several benefits, such as reduced transaction costs, faster transactions, and increased transparency. It also provides wider access to a larger pool of buyers and sellers, which can lead to better prices for both parties. Overall, tea e-auction is a useful tool for tea producers and buyers to trade their products in a more efficient and transparent manner. </p>
    </marquee>
  </h1>
</section>

     <section className="content">
  {/* Small boxes (Stat box) */}
  <div className="row">
    <div className="col-lg-12 col-xs-12">
      {/* small box */}
      <div className="small-box bg-aqua">
        <div className="inner maina">
          <h3 style={{color: '#d9f3c0', fontSize: '2vw'}} align="center">SMALL TEA GARDEN OWNERS AND TEA TRADERS ASSOCIATION</h3>
          <p align="center">PANCHAGARH</p>
          <p align="center">01711111111</p>
        </div>
      </div>
    </div>
    {/* ./col */}
    <div onClick={()=>history.push("/auction")} className="col-md-3 col-sm-6 col-xs-12">
      <div className="info-box">
        <span className="info-box-icon bg-aqua"><i className="ion ion-bag" /></span>
        <div className="info-box-content">
          <span className="text-bold text-uppercase" >Join Auction</span>
          <span className="info-box-number">kg {/*?php echo $SellT;?*/}</span>
        </div>
        {/* /.info-box-content */}
      </div>
      {/* /.info-box */}
    </div>
    <div className="col-md-3 col-sm-6 col-xs-12">
      <div className="info-box">
        <span className="info-box-icon bg-red"><i className="ion ion-bag" /></span>
        <div className="info-box-content">
          <span className="text-bold text-uppercase">Total Sold</span>
          <span className="info-box-number">kg {/*?php echo $csellT;?*/}</span>
        </div>
        {/* /.info-box-content */}
      </div>
      {/* /.info-box */}
    </div>
    <div className="col-md-3 col-sm-6 col-xs-12">
      <div className="info-box">
        <span className="info-box-icon bg-green"><i className="fa fa-group " /></span>
        <div className="info-box-content">
          <span className="text-bold text-uppercase">Total Unsold</span>
          <span className="info-box-number">kg {/*?php echo $custot;?*/}</span>
        </div>
        {/* /.info-box-content */}
      </div>
      {/* /.info-box */}
    </div>
    <div className="col-md-3 col-sm-6 col-xs-12">
      <div className="info-box">
        <span className="info-box-icon bg-red"><i className="fa fa-minus-square-o" /></span>
        <div className="info-box-content">
          <span className="text-bold text-uppercase">Total </span>
          <span className="info-box-number">kg {/*?php echo $exp;?*/}</span>
        </div>
        {/* /.info-box-content */}
      </div>
      {/* /.info-box */}
    </div>
  </div>
  {/* /.row (main row) */}
</section>  
        </>
    )
}

export default Dashboard
