import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";

import axios, * as others from "axios";
import swal from "sweetalert";
const initialState = {
  loading: false,
  accounts_data: [],
  status: "idle",
  error: false,
  edit:false,
  refresh:false,
};

const API_URL = process.env.REACT_APP_SERVER_URL;
// console.log(`${API_URL}/login`);
export const saveData = createAsyncThunk("ac/create", async (accounts) => {
  const response = await axios.post(`${API_URL}/create`, {
    adata: accounts
  });
  return response.data;
});

export const updateData = createAsyncThunk("ac/updatedata", async (accounts) => {
//  console.log(lcpCart);
  const response = await axios.put(`${API_URL}/update`, {
    adata: accounts,
  });
  return response.data;
});

export const selectData = createAsyncThunk("ac/selectdata", async (props) => {
  const response= await axios.put(`${API_URL}/selectacccount`,{
    id: props,
  });
    return response.data;
  });
  export const deleteData = createAsyncThunk("ac/deletedata", async (props) => {
 
    const response =axios.put(`${API_URL}/deleteaccount`,{
      id: props,
    })
    return response.data;
      
    });
 
    
const accountSlice = createSlice({
  name: "accounts",
  initialState,
  extraReducers: {
    [saveData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [saveData.fulfilled]: (state, action) => {
      const { payload } = action;
    },
    [saveData.rejected]: (state) => {
      state.error = true;
    },
    [updateData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [updateData.fulfilled]: (state) => {
      state.edit=false;
    },
    [updateData.rejected]: (state) => {
      state.error = true;
    },
    [selectData.fulfilled]: (state,action) => {
      const { payload } = action;
      state.accounts_data = payload;
      state.edit=true;
    },
    [deleteData.pending]: (state) => {
      state.refresh=false;
    },
    [deleteData.fulfilled]: (state) => {
      state.refresh=true;
    },
  },
});

export const {} = accountSlice.actions;
const accountReducer = accountSlice.reducer;

export default accountReducer;
