import { configureStore } from "@reduxjs/toolkit";
import loginReducer from "./loginSlice";
import supplyReducer from "./supplySlice";
import getdataReducer from "./getDataSlice";
import lcpurReducer from "./lcpurSlice";
import accountReducer from "./accountsSlice";
import lcopeningReducer from "./lcopeningSlice";
import auctionReducer from "./auctionSlice";
import catalogueReducer from "./catalogueSlice";
const store = configureStore({
  reducer: {
    login: loginReducer,
    supply: supplyReducer,
    getdata:getdataReducer,
    lcpur:lcpurReducer,
    accounts:accountReducer,
    lcaccount:lcopeningReducer,
    auction: auctionReducer,
    catalogue:catalogueReducer
  }
});

export default store;