import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios, * as others from "axios";
import swal from "sweetalert";
const initialState = {
  loading: false,
  supply_cart: [],
  lc_cart: [],
  status: "idle",
  error: false,
  edit:false,
  refresh:false,
};

const API_URL = process.env.REACT_APP_SERVER_URL;
export const saveData = createAsyncThunk("lc/savedata", async (cart,actype) => {
  const response = await axios.post(`${API_URL}/lcpurchase`, {
    adata: cart,
    actype: actype,
  });
  return response.data;
});

export const updateData = createAsyncThunk("lc/updatedata", async (lcpCart) => {
//  console.log(lcpCart);
  const response = await axios.put(`${API_URL}/updatelcpurchase`, {
    adata: lcpCart,
  });
  return response.data;
});

export const selectData = createAsyncThunk("lc/selectdata", async (props) => {
  const response= await axios.put(`${API_URL}/selectlcpurchase`,{
    id: props,
  });
    return response.data;
  });
  export const deleteData = createAsyncThunk("lc/deletedata", async (props) => {
 
    const response =axios.put(`${API_URL}/deletesellb2`,{
      id: props,
    })
    return response.data;
      
    });
 
    
const lcpurSlice = createSlice({
  name: "lcpur",
  initialState,
  extraReducers: {
    [saveData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [saveData.fulfilled]: (state, action) => {
      const { payload } = action;
    },
    [saveData.rejected]: (state) => {
      state.error = true;
    },
    [updateData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [updateData.fulfilled]: (state) => {
      state.edit=false;
    },
    [updateData.rejected]: (state) => {
      state.error = true;
    },
    [selectData.fulfilled]: (state,action) => {
      const { payload } = action;
      state.lc_cart = payload;
      state.edit=true;
    },
    [deleteData.pending]: (state) => {
      state.refresh=false;
    },
    [deleteData.fulfilled]: (state) => {
      state.refresh=true;
    },
  },
});

export const {} = lcpurSlice.actions;
const lcpurReducer = lcpurSlice.reducer;

export default lcpurReducer;
